<template>
  <div>

    <Header v-show="showHeader"></Header>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <div v-if="loading"><Spinner></Spinner></div>


    <div v-show="vm.isPlayingGame">
      <canvas id="myCanvas" width="600" height="500" style="border:1px solid #000000;">
      </canvas>
    </div>


    <p v-show="vm.currentScreen == 'LEVELS' || vm.currentScreen == 'CUSTOM_OBJECT_TYPES' || vm.currentScreen == 'CUSTOM_TEXTS' || vm.currentScreen == 'GLOBAL_VARIABLES' || vm.currentScreen == 'EVENTS' || vm.currentScreen == 'IMPORT_EXPORT' || vm.currentScreen == 'SAVE'">
      <button type="button" class="btn btn-light" v-on:click="showHeader=true" v-if="!vm.isPlayingGame && !showHeader"><i class="fas fa-angle-down"></i> Show header</button>
      <button type="button" class="btn btn-light" v-on:click="showHeader=false" v-if="!vm.isPlayingGame && showHeader"><i class="fas fa-angle-up"></i> Hide header</button>
      <button type="button" class="btn btn-success" v-on:click="vm.initGame();" v-if="!vm.isPlayingGame"><i class="fas fa-play-circle"></i> Play game</button>
      <button type="button" class="btn btn-primary" v-on:click="vm.initGame();" v-if="vm.isPlayingGame"><i class="fas fa-redo-alt"></i> Restart</button>
      <button type="button" class="btn btn-primary" v-on:click="vm.editGame()" v-if="vm.isPlayingGame"><i class="fas fa-edit"></i> Edit game</button>
      <button type="button" class="btn btn-primary" v-on:click="vm.newGame()" v-if="!vm.isPlayingGame"><i class="fas fa-plus"></i> New game</button>
      <button type="button" class="btn btn-primary" v-on:click="vm.requestCanvasFullscreen()" v-if="vm.isPlayingGame"><i class="fas fa-expand-arrows-alt"></i> Fullscreen</button>
    </p>


    <div v-show="!vm.isPlayingGame">


      <ul class="nav nav-tabs justify-content-center" v-show="vm.currentScreen == 'LEVELS' || vm.currentScreen == 'CUSTOM_OBJECT_TYPES' || vm.currentScreen == 'CUSTOM_TEXTS' || vm.currentScreen == 'GLOBAL_VARIABLES' || vm.currentScreen == 'EVENTS' || vm.currentScreen == 'IMPORT_EXPORT' || vm.currentScreen == 'SAVE'">
        <li class="nav-item">
        <a class="nav-link active" href="" v-on:click.prevent="vm.setCurrentTab('LEVELS');" id="link-tab-LEVELS">Levels</a>
        </li>
        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('CUSTOM_OBJECT_TYPES');" id="link-tab-CUSTOM_OBJECT_TYPES">Object Types</a>
        </li>
        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('CUSTOM_TEXTS');" id="link-tab-CUSTOM_TEXTS">Texts</a>
        </li>

        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('GLOBAL_VARIABLES');" id="link-tab-GLOBAL_VARIABLES">Global variables</a>
        </li>
        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('EVENTS');" id="link-tab-EVENTS">Events</a>
        </li>
        
        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('IMPORT_EXPORT');" id="link-tab-IMPORT_EXPORT">Export</a>
        </li>
        
        <li class="nav-item">
        <a class="nav-link" href="" v-on:click.prevent="vm.setCurrentTab('SAVE');" id="link-tab-SAVE">Save Online</a>
        </li>
      </ul>
      <br>

    

      <Levels :vm="vm"></Levels>
      <LevelsTab :vm="vm"></LevelsTab>
      <EditCustomObject :vm="vm"></EditCustomObject>
      <EditButton :vm="vm"></EditButton>
      <EditLevelVariable :vm="vm"></EditLevelVariable>
      <CustomObjectTypes :vm="vm"></CustomObjectTypes>
      <CustomObjectTypesTab :vm="vm"></CustomObjectTypesTab>
      <CustomObjectTypeBehaviour :vm="vm"></CustomObjectTypeBehaviour>
      <EditObjectVariable :vm="vm"></EditObjectVariable>
      <GlobalVariables :vm="vm"></GlobalVariables>
      <EditGlobalVariable :vm="vm"></EditGlobalVariable>
      <CustomTexts :vm="vm"></CustomTexts>
      <EditCustomText :vm="vm"></EditCustomText>
      <Events :vm="vm"></Events>
      <EditEvent :vm="vm"></EditEvent>
      <EditLoop :vm="vm"></EditLoop>
      <EditEventCondition :vm="vm"></EditEventCondition>
      <EditEventAction :vm="vm"></EditEventAction>
      <ImageSelection :vm="vm"></ImageSelection>
      <ImportExport :vm="vm"></ImportExport>
      <SaveOnline :vm="vm"></SaveOnline>


    </div>



  <InsertExpression :vm="vm"></InsertExpression>


  <div v-if="vm.environment" class="engineVersion">Engine version: {{vm.environment.templateVersion}}</div>

  </div>
</template>


<script>
import axios from 'axios';
//import {getCookie} from '@/lib/mmgUtil.js';
import {loadEngine} from '@/lib/mmgUtil.js';
import Header from '@/components/Header.vue';
import ImportExport from './components/ImportExport.vue';
import Levels from './components/Levels.vue';
import LevelsTab from './components/LevelsTab.vue';
import EditLevelVariable from './components/EditLevelVariable.vue';
import CustomObjectTypes from './components/CustomObjectTypes.vue';
import EditCustomObject from './components/EditCustomObject.vue';
import EditButton from './components/EditButton.vue';
import CustomObjectTypesTab from './components/CustomObjectTypesTab.vue';
import CustomObjectTypeBehaviour from './components/CustomObjectTypeBehaviour.vue';
import EditObjectVariable from './components/EditObjectVariable.vue';
import GlobalVariables from './components/GlobalVariables.vue';
import EditGlobalVariable from './components/EditGlobalVariable.vue';
import CustomTexts from './components/CustomTexts.vue';
import EditCustomText from './components/EditCustomText.vue';
import Events from './components/Events.vue';
import EditEvent from './components/EditEvent.vue';
import EditLoop from './components/EditLoop.vue';
import EditEventCondition from './components/EditEventCondition.vue';
import SaveOnline from './components/SaveOnline.vue';
import EditEventAction from './components/EditEventAction.vue';
import InsertExpression from './components/InsertExpression.vue';
import ImageSelection from './components/ImageSelection.vue';
import Spinner from '@/components/Spinner.vue';
//import {setCookie} from '@/lib/mmgUtil.js';
//import {buildViewModel} from '@/lib/GameEditorViewModel.js';
//import {ActionTemplateController} from '@/lib/mmg/snapshot/ActionTemplateController.js';

export default {
  name: 'EditGame',
  components: {
    Spinner,
    Header, ImportExport, Levels, LevelsTab, EditLevelVariable, CustomObjectTypes, EditCustomObject, EditButton,
    CustomObjectTypesTab, CustomObjectTypeBehaviour, EditObjectVariable, GlobalVariables, EditGlobalVariable,
    CustomTexts, EditCustomText, Events, EditEvent, EditLoop, EditEventCondition, SaveOnline, EditEventAction,
    InsertExpression, ImageSelection
  },
  methods: {

    getGame: function (gameId) {

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'games/' + gameId;
      const config = {};

      return axios.get(url, config);

    }
    
  },
  data() {
    return {
      game: null,
      loading: false,
      errorMessage: null,
      showHeader: false,
      info: null,
      sessionId: null,
      saveGameForm: {
        gameId: "",
        gameParamsJson: ""
      },
      showForm: true,
      gameDetails: {
        name: "",
        userId: "",
        createdDate: "",
        updatedDate: ""
      },
      vm: {
        isPlayingGame: false,
        oldLevel: null,
        newLevelVariable: null
      }
    }
  },
  mounted () {

    let that = this;

    that.loading = true;

    let gameId = that.$route.query.gameId;

    if (gameId != null) {

      this
        .getGame(gameId)
        .then(function(response) {
          that.game = response.data.Item;
          let templateVersion = that.game.templateVersion.S;
          return loadEngine(templateVersion);
        })
        .then(function() {
          let newVm = new ActionTemplateController(process.env.VUE_APP_RESOURCES_BASE_URL); // eslint-disable-line no-undef
          newVm.setGame(that.game);

          that.vm = newVm;
          that.loading = false;
        });

    } else {

      let templateVersion = "v0.0.24";

      loadEngine(templateVersion)
        .then(function() {
          let newVm = new ActionTemplateController(process.env.VUE_APP_RESOURCES_BASE_URL); // eslint-disable-line no-undef

          that.vm = newVm;
          that.loading = false;
        });

    }

  },
  destroyed () {
    this.vm.removeEventListeners();
  }
}
</script>


<style>

table {
    margin: 0 auto;
}

.engineVersion {
  font-size: 10px;
  margin-top: 20px;
}

</style>